*, :before, :after {
  box-sizing: border-box;
}

html {
  -moz-text-size-adjust: none;
  text-size-adjust: none;
}

body, h1, h2, h3, h4, p, figure, blockquote, dl, dd {
  margin-block-end: 0;
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  min-height: 100vh;
  line-height: 1.5;
}

h1, h2, h3, h4, button, input, label {
  line-height: 1.1;
}

h1, h2, h3, h4 {
  text-wrap: balance;
}

a {
  text-decoration-skip-ink: auto;
  color: currentColor;
  text-decoration: none;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font-family: inherit;
  font-size: inherit;
  padding: 0;
}

textarea {
  min-height: 10em;
}

:target {
  scroll-margin-block: 5ex;
}

header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.virtual__title, .motivation__title {
  flex-flow: column wrap;
  align-items: center;
  line-height: 1.4;
  display: flex;
}

.chronick__title span:before, .virtual__title span:after, .motivation__title span:after {
  content: "";
  border-radius: 48px;
  width: 120px;
  height: 34px;
  display: inline-block;
}

.statistic > picture, .coworkerHero picture {
  border-radius: 24px;
  display: inline-block;
  overflow: hidden;
}

.question__caption, .client__caption, .statistic__caption, .virtual__caption {
  text-transform: uppercase;
  margin-bottom: 16px;
  font-family: General-Sans-semibold, sans-serif;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.5;
}

html {
  scroll-padding-top: 4rem;
