body:has([popover]:popover-open) {
  background-color: #1d1e1c;
  overflow-y: hidden;
}

body:has([popover]:popover-open) header {
  position: sticky;
  top: 0;
}

body:has([popover]:popover-open) .logo {
  color: #fff;
  font-size: 26px;
}

body:has([popover]:popover-open) .headerBurger {
  border: none;
  border-radius: 0;
  padding-right: 20px;
}

body:has([popover]:popover-open) .headerBurger div {
  width: 32.54px;
  margin: 0;
  rotate: -45deg;
}

body:has([popover]:popover-open) .headerBurger div:last-child {
  rotate: 45deg;
}

body:has([popover]:popover-open) .headerBurger div:nth-child(2) {
  display: none;
}

[popover]:popover-open {
  opacity: 1;
  transition: transform .5s, opacity .5s, display .5s, overlay .5s;
  transform: translateY(0);
}

@starting-style {
  [popover]:popover-open {
    opacity: 0;
    transform: translateY(100vh);
  }
}

[popover]:not(:popover-open) {
  opacity: 0;
  transition: transform .5s, opacity .5s, overlay .5s allow-discrete, display .5s allow-discrete;
  transform: translateY(100vh);
}

@media (width >= 780px) {
  [popover]:not(:popover-open) {
    all: initial;
  }
}

